@import "../../../../styles/variables/variables";

.container {
  margin-bottom: 32px;
}

.inputWrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.label {
  display: inline-block;
  overflow: hidden;
  max-width: 105px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  color: $main-black;
  margin-right: 15px;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
  line-height: 16px;
}

.title {
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  margin: 0 0 16px;
}